*{
  font-family: Arial;
  padding: 6px;
  margin: 0%;

}

.container{
  background-color: #24232b;
  padding: 24px;

}

.header{
  color: white;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 20px;

}

button{
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #2a8b8b;
  color: white;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;

}

.copybtn{
  font-size: 10px;
  height: 30px;
}

.charlength{
  display: flex;
  flex-direction: column;
  padding-bottom: 24px;
}

.charlength span{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-bottom: 24px;
}

.charlength span label{
  color: white;
  font-size: 24px;
  font-weight: 600;
}

.generaterbtn{
  width: 100%;
  font-size: 20px;
  padding: 20px;

}

.generaterbtn:hover{
  background-color: #2a8b8b4f;
}

.checkboxes{
  display: grid;
  grid-template-columns: auto auto;
  font-size: 16px;
  font-weight: 700;
}

.checkboxes div{
  color: white;
  display: flex;
  grid-template-columns: auto auto;
  gap: 5px;
  padding-bottom: 25px;
}

.errorMessage{
  color: red;
  margin-bottom: 5px;
}

.color-red{
  font-size: 1rem;
  font-weight: 600;
  color: white;
}
